.form input,
.form select,
.form textarea {
  display: block;
  margin: 0 auto 20px;
  padding: 0 6px;
  width: 90%;
  height: 50px;
  font-size: 16px;
  color: #2f3e9c;
  background: #fff;
  border: 3px solid #4ad9db;
  border-radius: 2px;
}
.form button,
.form a.button {
  display: inline-block;
  width: 43%;
  line-height: 38px;
  font-size: 18px;
  font-weight: bold;
  border: none;
  color: #fff;
  background: #4ad9db;
  opacity: 0.9;
}
.form input[type=file] {
  height: auto;
  padding: 10px;
}
.form textarea {
  padding: 10px 5px;
  resize: none;
  height: 90px;
  line-height: 22px;
  overflow: hidden;
}

.loginForm {
  background: #f4eba0;
  height: 50%;
  position: relative;
  padding-top: 40px;
}
.loginForm button {
  margin-left: 5%;
  float: left;
}
.loginForm a.button {
  margin-right: 5%;
  float: right;
}
.loginForm .errors {
  position: absolute;
  top: 0;
  left: 5%;
  transform: translateY(-50%);
}